.Z_imgs {
  position: absolute;
  /* box-shadow: 0px 10px 0px 375px rgb(0 0 0 / 30%); */
  top: -120px;
  z-index: 10;
}
.Z_imgs > div:first-child {
  width: 100%;
  position: relative;
  z-index: 1000;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 30px;
}
.Z_imgs > div:first-child img {
}
.Z_imgs img:nth-child(2) {
  width: 406px;
  border-radius: 12px;
  box-shadow: 0px 10px 0px 3705px rgb(0 0 0 / 60%);
}

.Z_imgs .thumbnail {
  display: flex;
  gap: 30px;
  margin-top: 17px;
}
.Z_imgs .thumbnail img {
  width: 78px;
  border-radius: 12px;
  cursor: pointer;
}
.Z_imgs .thumbnail img:hover {
  opacity: 0.6;
}
.Z_imgs .thumbnail span {
  border-radius: 15px;
  height: 75px;
}

.op-img {
  opacity: 0.4;
}
.slide {
  display: flex;
  align-items: center;
  /* box-shadow: 0px 10px 0px 375px rgb(0 0 0 / 30%); */
}
.slide span {
  background-color: white;
  border-radius: 50px;
  width: 35px;
  height: 35px;
  position: absolute;
  right: 15px;
  z-index: 100;
}
#next img,
#prev img {
  margin: 8px 8px 8px 12px;
}
.slide span:nth-child(3) {
  background-color: white;
  position: absolute;
  right: -9px;
  width: 35px;
  height: 35px;
  border-radius: 100px;
}
.slide span:nth-child(1) {
  position: absolute;
  left: -15px;
  background-color: white;
  width: 35px;
  height: 35px;
  border-radius: 100px;
}
.slide span:first-child img {
  margin: 8px;
}
.slide span:nth-child(3) img {
  margin: 8px auto auto 13px;
}
.border_img {
  background-color: white;
}
@media only screen and (min-width: 500px) and (max-width: 700px) {
  .Z_imgs {
    top: 20px;
  }
}
