body {
  margin: 0;
  padding: 0;
}
.head {
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: white;
}
header {
  display: flex;
  padding-left: 200px;
  padding-right: 200px;
  padding-top: 20px;
  justify-content: space-between;
}
header .cont {
  font-family: "Kumbh Sans";
  font-family: "", cursive;
  display: flex;
  align-items: center;
  gap: 30px;
}
header div h1 {
  margin-top: 10px;
  font-size: 25px;
}
header div p {
  color: hsl(219, 9%, 45%);
  cursor: pointer;
}
hr {
  width: 73%;
  color: hsl(219, 9%, 45%);
}
.line {
  width: 60px;
  height: 4px;
  background-color: #ff7e1b;
  border-radius: 10px;
  margin-bottom: 0;
  visibility: hidden;
}
p:active {
  color: black;
}
p:hover ~ div {
  visibility: visible;
}
hr {
  margin-top: 0;
  color: hsl(219, 9%, 45%);
}
.right-div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100px;
}
.right-div img {
  cursor: pointer;
}
.right-div img:last-child {
  border: 2px solid #ff7e1b;
  border-radius: 50%;
}
.cart-show {
  width: 270px;
  position: absolute;
  right: 10%;
  top: 100%;
  box-shadow: 1px 10px 30px rgb(122, 122, 122);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 170px;
  z-index: 20;
  background-color: white;
}
.cart-show h4 {
  font-size: 0.9em;
  font-family: "Kumbh Sans";
  margin-left: 5%;
  margin-bottom: 10px;
  z-index: 9;
}
.cart-show hr {
  width: 95%;
  opacity: 0.4;
}
.cart-prod {
  display: flex;
}
.cart-prod img {
  width: 40px;
  border-radius: 5px;
  margin-left: 10px;
  height: 40px;
}
#delet {
  width: 12px;
  height: 14px;
  margin-top: 5%;
  margin-left: 5 px;
}
.cart-prod p {
  margin: 0;
  font-family: "Kumbh Sans";
  color: hsl(219, 9%, 45%);
  font-size: 12px;
  margin-left: 14px;
  text-align: initial;
  width: 170px;
  font-weight: 500;
}
.cart-prod p span {
  font-weight: bold;
  color: black;
}
.cart-show button {
  margin-top: 10px;
  width: 85%;
  align-self: center;
  height: 40px;
  background-color: hsl(26, 100%, 55%);
  border: none;
  border-radius: 10px;
  color: wheat;
  font-weight: 800;
  font-size: 12px;
}
nav {
  display: flex;
  gap: 30px;
}
#menu {
  display: none;
}
#close {
  display: none;
}
.root {
  background-color: white;
  height: 100vh;
}
@media only screen and (min-width: 1600px) and (max-width: 2400px) {
  header {
    padding-left: 100px;
    padding-right: 100px;
  }
  hr {
    width: 90%;
  }
}
@media only screen and (min-width: 1000px) and (max-width: 1300px) {
  header {
    padding-left: 100px;
    padding-right: 100px;
  }
  hr {
    width: 83%;
  }
}
@media only screen and (min-width: 600px) and (max-width: 1000px) {
  header {
    padding-left: 30px;
    padding-right: 30px;
  }
  hr {
    width: 90%;
  }
  header .cont {
    gap: 10px;
  }
  nav {
    gap: 1px;
  }
}

@media only screen and (min-width: 365px) and (max-width: 600px) {
  header {
    padding-left: 30px;
    padding-right: 30px;
  }
  hr {
    width: 90%;
  }
  header .cont {
    gap: 10px;
  }
  nav {
    flex-direction: column;
    position: absolute;
    gap: 1px;
    width: 200px;
    height: 190vh;
    background-color: white;
    z-index: 100;
    left: 0;
    top: 0px;
    padding-top: 30px;
    align-items: flex-start;
    padding-left: 50px;
    text-align: initial;
    opacity: 1;
    border-right: 1px solid black;
    box-shadow: 256px 10px 0px 105px rgb(0 0 0 / 30%);
  }
  #menu {
    display: inline;
    margin-right: 10%;
  }
  .menu-icon {
    display: flex;
    align-items: center;
  }
  header div p {
    color: black;
    font-weight: 700;
    font-size: 24px;
  }
  #close {
    display: block;
    margin-bottom: 30px;
  }
  hr {
    margin-bottom: 0;
  }
  .cart-show {
    width: 90%;
    right: 4%;
    height: 290px;
    top: 115%;
  }
  .cart-show h4 {
    font-size: 1.6em;
    font-family: "Kumbh Sans";
    margin-left: 5%;
    margin-bottom: 23px;
    z-index: 9;
  }
  .cart-show hr {
    margin-bottom: 26px;
  }
  .cart-show img {
    width: 60px;
    border-radius: 5px;
    margin-left: 23px;
    height: 60px;
    margin-right: 23px;
  }
  .cart-prod {
    width: 100%;
    justify-content: center;
  }
  .cart-prod p {
    width: 103%;
    font-size: 17px;
  }
  .cart-show button {
    height: 57px;
    margin-top: 28px;
  }
}
#empty {
  width: 100%;
  padding-top: 24px;
  font-family: "Kumbh Sans";
}
