.body {
  margin-top: 60px;
  text-align: initial;
  display: flex;
  justify-content: center;
  font-family: "Kumbh Sans";
  position: relative;
  z-index: 2;
  background-color: white;
}
.body .imgs img:nth-child(2) {
  width: 350px;
  border-radius: 12px;
  cursor: pointer;
}
.body .imgs .thumbnail {
  display: flex;
  gap: 30px;
  margin-top: 20px;
}
.body .imgs .thumbnail img {
  width: 64px;
  border-radius: 12px;
  cursor: pointer;
}
.body .imgs .thumbnail img:hover {
  opacity: 0.6;
}
.body .imgs .thumbnail span {
  border-radius: 15px;
  height: 64px;
}
.border_img {
  border: 2px solid hsl(26, 100%, 55%);
}
.op-img {
  opacity: 0.4;
}
.content {
  margin-left: 10%;
  padding-top: 15px;
  width: 345px;
}
.content p {
  font-family: "Kumbh Sans";
  color: hsl(26, 100%, 55%);
  font-size: 0.7em;
  font-weight: 900;
  letter-spacing: 1px;
}
.content h1 {
  font-family: "Kumbh Sans";
  margin-top: 0.2em;
}
.content p:nth-child(3) {
  font-size: 0.9em;
  color: hsl(219, 9%, 45%);
}
.content h2 span {
  background-color: hsl(25, 100%, 94%);
  font-size: 16px;
  margin-left: 20px;
  color: hsl(26, 100%, 55%);
}
.content p:nth-child(5) {
  font-size: initial;
  color: hsl(220, 14%, 75%);
  text-decoration: line-through;
}
.btn {
  display: flex;
  align-items: center;
  align-content: center;
}
.btn div {
  background-color: hsl(223, 64%, 98%);
  width: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}
.btn span:first-child {
  color: hsl(26, 100%, 55%);
  cursor: pointer;
}
.btn span:last-child {
  cursor: pointer;
}
.btn button img {
  width: 15px;
  margin-right: 10px;
  /* color: blueviolet; */
}
.btn button {
  background-color: hsl(26, 100%, 55%);
  margin-left: 5%;
  height: 40px;
  width: 200px;
  border: none;
  border-radius: 12px;
  color: white;
  font-weight: 700;
}
#next {
  display: none;
}
#prev {
  display: none;
}
@media only screen and (min-width: 365px) and (max-width: 600px) {
  .body {
    margin-top: 0;
    flex-direction: column;
  }
  .body .imgs img:nth-child(2) {
    width: 100%;
    margin-top: 0;
  }
  #slide {
    display: flex;
    align-items: center;
    position: relative;
  }
  #next,
  #prev {
    display: initial;
    background-color: white;
    border-radius: 50px;
    width: 35px;
    height: 35px;
    position: absolute;
    right: 15px;
  }

  #next img,
  #prev img {
    margin: 8px 8px 8px 12px;
  }
  #prev {
    position: absolute;
    left: 15px;
    z-index: 10;
  }
  .content h1 {
    font-size: 3em;
    margin-bottom: 10px;
  }
  .content p {
    font-size: 1em;
  }
  .content p:nth-child(3) {
    font-size: 1.1em;
  }
  .content {
    width: 90%;
    margin-left: 20px;
    margin-bottom: 69px;
  }
  .content h2 {
    display: inline;
    font-size: 2.4em;
  }
  .content h2 span {
    font-size: 26px;
    margin-left: 18px;
  }
  .content p:nth-child(5) {
    display: inline;
    float: right;
    line-height: 14px;
    font-size: 1.4em;
  }
  .btn {
    margin-top: 50px;
    flex-direction: column;
  }
  .btn div {
    width: 90%;
    height: 100px;
    margin-top: -20px;
  }
  .btn div span img {
    width: 45px;
    margin-left: 40%;
  }
  .btn button {
    width: 90%;
    height: 70px;
    font-size: 1.3em;
    margin: auto;
    margin-top: 11px;
  }
  .btn button img {
    width: 28px;
  }
}
@media only screen and (min-width: 601px) and (max-width: 800px) {
  .body {
    margin-top: 0;
    flex-direction: column;
    align-items: center;
  }
}

@media only screen and (max-width: 375px) {
  .content h1 {
    font-size: 2.2em;
  }
}
